import React, { useState } from 'react';

import axios from 'axios';

import { googleLogout, useGoogleLogin, GoogleLogin } from '@react-oauth/google';

function BaseGeneric(props) {
    const [ profile, setProfile ] = useState(null);
    
    const gAuthDebug = false;
    
    const login = (res) => {
        if (gAuthDebug) 
            console.log(res);
        else {
            //axios.post('http://localhost:3001/auth', { res })
            axios.post('/auth', { res })
            .then(
                auth_res => {
                    setProfile(auth_res.data);
                    //let id_token = res.data.getAuthResponse().id_token;
                    console.log(auth_res.data)
                }
            );
        }
    }

    // log out function to log the user out of google and set the profile array to null
    const logOut = () => {
        googleLogout();
        setProfile(null);
    };
    
    //console.log('profile is: ', profile);

    return (
<>
    <header className = 'header'>
        <section name = 'header-content' className = 'container header-content'>
            <h2>
                Slides app
            </h2>

            {profile ? (
            <div className='auth-div'>
                <button className='auth' onClick={logOut}>
                    Log out
                </button>
                <img className='avatar' src={profile.picture} alt="user image" />
            </div>

            ) : (

            <GoogleLogin onSuccess = {login} />

            )}
        </section>
    </header>
    <main className = 'main'>
        <div className = 'container main-content'>
            { props.children }
        </div>
    </main>

    <footer>

    </footer>

</>
    );
}

export default BaseGeneric;