import React from "react"
import ReactDom from "react-dom"

import { GoogleOAuthProvider } from '@react-oauth/google';

import App from "./app"

let root = document.querySelector('#root');
ReactDom.render(
    <GoogleOAuthProvider clientId="871435621707-t4kpqbspt65v8767hnfdnanri391cu2l.apps.googleusercontent.com">
        <React.StrictMode>
            <App />
        </React.StrictMode>
    </GoogleOAuthProvider>, 
root);

