import React from "react"

import BaseGeneric from './components/base_generic';
import Editor from './components/editor';

class App extends React.Component
{
    constructor(props)
    {
        super(props);
        
        this.state = new Object();
        this.state.role = 'none';
        this.state.debug = true;
        
        this.state.user = new Object();
    }
    
    render()
    {
        const role = this.state.role;
        
        let content = <Editor/>;
        //let content = 'uhuh what';
        
        //if (role == 'superuser')  content = <SuperUser root = { this } />;
        //else if (role == 'admin') content = <Admin root = { this } />;
        //else if (role == 'user')  content = <User root = { this } />;
        
        return (
            <BaseGeneric>
                { content }
            </BaseGeneric>
        );
    }
}

export default App;
